<template>
  <div class="cube-tab" :class="{'cube-tab_active': isActive}" @click="handleClick(index)">
    <slot name="icon">
      <i :class="icon"></i>
    </slot>
    <slot>
      <div class="label">{{label}}</div>
    </slot>
  </div>
</template>
<script type="text/ecmascript-6">
const COMPONENT_NAME = "cube-tab";

export default {
  name: COMPONENT_NAME,
  props: {
    label: {
      type: [String, Number],
      required: true
    },
    icon: {
      type: String,
      default: ""
    },
    index: {
      type: Number
    }
  },
  mounted() {
    this.$parent.addTab(this);
  },
  destroyed() {
    this.$parent.removeTab(this);
  },
  computed: {
    isActive() {
      return this.$parent.value === this.label;
    }
  },
  methods: {
    handleClick(index) {
      // 如果value change，还会触发input和change事件
      this.$parent.trigger(this.label, index);
    }
  }
};
</script>
<style lang="stylus" rel="stylesheet/stylus">
@require '../../common/stylus/variable.styl';

.cube-tab {
  flex: 1;
  padding: 7px 0;
  color: $tab-color;
  text-align: center;
}

.cube-tab_active {
  color: $tab-active-color;
}
</style>
